WL.registerComponent('ball', {
    param: {type: WL.Type.Float, default: 1.0},
}, {
    init: function() {
    },
    start: function() {
        this.translation = this.object.transformWorld
    },
    update: function(dt) {
        if (this.translation[5] < -0.9 ) {
            this.object.destroy();
        }
    },
});
